body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.side-bar
{
    height: 100%;
    width: 70px;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background-color: #111;
    overflow-x: hidden;
    padding-top: 100px;
}
.link
{
    display:block;
    padding-top: 150px;
    text-decoration: none;
}
.icon
{
 width: 45px;
 height: 45px;
}


.App {
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}

@media only screen and (max-width: 768px) {
  /* For mobile phones: */
 .App{
   margin-left:80px;

 }
}



.profile-container
{
    padding-left:80px;
    display:flex;
    flex-direction: column;
    flex-wrap: wrap;
    float:left;
    border-right: 2px groove rgba(28,110,164,0.22);
    height: 100vh;
}
.Profile
{
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}
.profile-picture
{
    background: #fff;
    border: 5px solid #fff;
    padding-top: 20px;
}

.picture
{
    width:15vh;
    height:15vh;
    border-radius: 50%;
}
.friends-title
{
    font-weight: bold;
    text-align: left;
    padding-left: 10px;
}
.bio
{
    font-size: 16px;
    color:grey;
    width:20vh;
    text-align: left;
    padding:10px 0 10px 10px;
}

.friends
{
    display:grid;
    flex-direction: column;
    flex-wrap:wrap;
    overflow: hidden;
}

.main
{
    overflow:auto;
    padding-left: 50px;
    margin-left:1px;
    width:30vh;
    border-right: 2px groove rgba(28,110,164,0.22);
}

.profile-username
{
    font-size:30px;
    font-weight: bold;
    padding-left:10px;
    -webkit-margin-before: 0em;
            margin-block-start: 0em;
    -webkit-margin-after: 0em;
            margin-block-end: 0em;
    -webkit-margin-start: 0px;
            margin-inline-start: 0px;
    -webkit-margin-end: 0px;
            margin-inline-end: 0px;
}
.posts-title
{
 padding-left:7.5vh;
 text-decoration: underline;
}


.statistics
{
 width:auto;
 display: flex;
 flex-direction: column;
}


.pie-chart
{
    width: 500px;
    height: 400px;
    padding-left:100px;
}
.tags
{
    display: flex;
    flex-direction: row;
}
.tag{
 padding-left:75px;
}
.block
{
    width:10px;
    height: 10px;
    display: inline-block;
    margin-right:105px;
}
.yellow
{
 background-color: #d7f226;
}
.red
{
 background-color:#c11b1b;
}
.green
{
    background-color: #0dc441;
}
.friend
{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width:200px;
    height:50px;
    padding-bottom: 20px;
}

.friend-image
{
    background: #fff;
    border: 5px solid #fff;
    height: 40px;
    width: 40px;
    
}
.image-40
{
    width:40px;
    height: 40px;
}

.friend-name
{
height: 40px;
}
.friend-link
{
    text-decoration: none;
    color:black;
}
.post-title
{
    font-size:20px;
    font-weight: 50;
    text-decoration: underline;
}
.post-content
{
    color:grey;
    font-size: 15px;
}
.post
{
    padding-top:10px;
    padding-right: 20px;
}

.user-stats
{
    width:300px;
    display:inline-block;
    cursor: pointer;
    background: #fefefe;
    padding: 10px 5px;
    border-radius: 5px;
    box-shadow: 0px 0px 10px 1px rgba(0, 0, 0, 0.1);
    display: flex;
    justify-content: space-between;
    align-items: center;
    -webkit-transition: -webkit-transform 0.1s ease-in-out;
    transition: -webkit-transform 0.1s ease-in-out;
    transition: transform 0.1s ease-in-out;
    transition: transform 0.1s ease-in-out, -webkit-transform 0.1s ease-in-out;
    margin:0 auto;
    z-index: 4;
}
.user-stats:hover
{ 
    -webkit-transform: scale(1.1); 
            transform: scale(1.1);
}
.username
{   
    display:inline-block;
}
.points
{
    color:green;
    font-size:15px;
    display: inline-block;
}
.image
{
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background: #ebeef3;
    overflow: hidden;
    padding: 5px;
}

.profile-link
{
    text-decoration: none;
    color:black;
    margin:100px;
}


.login-container
{
    height:100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.input
{
    font-size: 15px;
    line-height: 1.5;
    color: #666666;
    display: block;
    width: 100%;
    background: #e6e6e6;
    height: 50px;
    border-radius: 25px;
    padding: 0 30px 0 30px;
    outline: none;
    border: none;
}

.username-container
{
    margin-bottom: 10px;
}

.login-button-container
{
    display:flex;
    justify-content: center;
    align-items: center;
    margin-left: 50px;
}
.login-button
{
    background-color: #4CAF50;
    color: white;
    padding: 14px 20px;
    margin: 8px 0;
    border: none;
    cursor: pointer;
    width: 100%;
}
.login-button:hover
{
    opacity: 0.6;
}
